import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import ComponentIndex from './MyComponents/ComponentIndex';
import LoginPage from './MyComponents/LoginPage';
import CompA from './MyComponents/CompA';
import CompB from './MyComponents/CompB';
import CompC from './MyComponents/CompC';

ReactDOM.render(
    <>
        <BrowserRouter>
            <Switch>
                <Route exact path="/"> <LoginPage /> </Route>
                <Route exact path="/university-website"> <ComponentIndex /> </Route>
                <Route exact path="/comp1" component={CompA} > <ComponentIndex /> <CompA /> </Route>
                <Route exact path="/comp2" component={CompB} > <ComponentIndex /> <CompB /> </Route>
                <Route exact path="/comp3" component={CompC} > <ComponentIndex /> <CompC /> </Route>
            </Switch>
        </BrowserRouter>
    </>, document.getElementById("root")
);